<template>
	<b-modal id="WriteCalendarModal" dialog-class="full_modal no_static_height" hide-footer hide-header centered size="lg" @hidden="reset" @shown="shown">
		<template #default>
			<div class="header d-flex justify-content-between align-items-center">
				<span class="fs-lg">{{ $t("writeCalendar") }}</span>
				<BIcon icon="x" @click="reset" scale="0.8" class="btn_icon btn_icon_md text-muted" />
			</div>
			<div class="p-2 pb-5">
				<p class="fs-sm text-muted my-4 pb-1 pt-2">{{ $t("writeCalendarDesc") }}</p>
				<div class="mt-4">
					<form-select :options="writeCalendars" :label="$t('writeCalendar')" v-model="selectedCalendar" v-if="!addEventToCalendar">
						<BIcon icon="calendar3" slot="icon_left" />
					</form-select>
					<div class="d-flex align-items-center text-muted mt-5">
						<NotAllowIcon width="1.5rem" style="fill: #5e5e5e" class="mr-2" />
						<p class="mb-0">{{ $t("noWriteCalendar") }}</p>
						<div class="ml-auto">
							<b-checkbox switch v-model="addEventToCalendar"></b-checkbox>
						</div>
					</div>
				</div>
			</div>
			<div class="footer d-flex align-items-center justify-content-end">
				<b-btn class="filter_btn active mx-3" size="sm" @click="reset">{{ $t("cancel") }}</b-btn>
				<b-btn type="submit" variant="primary" @click="save">{{ $t("save") }}</b-btn>
			</div>
		</template>
	</b-modal>
</template>

<script>
	import FormSelect from "@/components/inputs/FormSelect";

	import NotAllowIcon from "@/components/icons/notAllow";

	export default {
		components: { FormSelect, NotAllowIcon },

		data() {
			return {
				isLoading: false,
				selectedCalendar: "",
				addEventToCalendar: false,
				calendarImages: {
					"microsoft.com": "microsoft.png",
					"google.com": "google_calendar.png",
				},
			};
		},

		computed: {
			calendars() {
				return this.$store.state.account.calendars;
			},

			writeCalendars() {
				let calendars = this.calendars.filter((calendar) => calendar.hasWritePermission);

				return calendars.map((calendar) => {
					return {
						value: calendar.calendarId,
						text: calendar.calendarName,
					};
				});
			},

			writeCalendar() {
				let writeCalendar = null;

				for (let integration of this.$me.integrations) {
					if (!integration.scopes.includes("calendar") || !integration.calendars.length) continue;

					for (let calendar of integration.calendars) {
						if (calendar.shouldWrite) {
							writeCalendar = calendar;
							break;
						}
					}
				}

				return writeCalendar;
			},
		},

		methods: {
			save() {
				this.$bvModal.hide("WriteCalendarModal");
			},

			shown() {
				this.selectedCalendar = this.writeCalendar ? this.writeCalendar.calendarId : "";
			},

			reset() {
				this.$bvModal.hide("WriteCalendarModal");
			},
		},
	};
</script>

