<template>
	<account activeTab="calendars">
		<section>
			<div class="field_title pt-1 d-flex align-items-center">
				<BIcon icon="arrow-repeat" class="mr-2" />
				<div class="title font-weight-500">{{ $t("sync") }}</div>
			</div>
			<div class="mt-3">
				<div class="mb-5">
					<div class="font-weight-normal d-flex align-items-center">
						<span>{{ $t("readCalendars") }}</span>
						<div class="d-flex align-items-center fs-md text-primary font-weight-500 ml-4 cursor-pointer" v-b-modal.ReadCalendarsModal>
							<b-icon icon="pencil" class="mr-2" width="1em"></b-icon>
							<span>{{ $t("edit") }}</span>
						</div>
					</div>
					<span class="text-muted fs-sm">{{ $t("readCalendarsDesc") }}</span>
					<div class="fs-sm mt-4">
						<div v-if="!readCalendars.length" class="d-inline-block py-2 px-3 rounded-pill" style="background-color: #ff001a1a">
							<div class="d-flex align-items-center">
								<BIcon icon="exclamation-circle" class="mr-2" variant="danger" scale="1.2" />
								<span class="text-muted">{{ $t("noReadCalendars") }}</span>
							</div>
						</div>
						<div v-for="(provider, i) in readCalendars" :key="i">
							<div class="d-flex align-items-center mb-2">
								<b-img :src="require(`../../assets/media/${provider.image}`)" width="16px"></b-img>
								<span class="text-muted ml-2">{{ provider.email }}</span>
							</div>
							<div v-for="(calendar, i) in provider.calendars" :key="i">
								<div class="d-flex align-items-center">
									<span class="mr-2 ml-3 rounded-circle" :style="{ backgroundColor: calendar.color, width: '8px', height: '8px' }"></span>
									<span>{{ calendar.name }}</span>
								</div>
							</div>
						</div>
					</div>
				</div>
				<div class="mb-4">
					<div class="font-weight-normal d-flex align-items-center">
						<span>{{ $t("writeCalendar") }}</span>
						<div class="d-flex align-items-center fs-md text-primary font-weight-500 ml-4 cursor-pointer" v-b-modal.WriteCalendarModal>
							<b-icon icon="pencil" class="mr-2" width="1em"></b-icon>
							<span>{{ $t("edit") }}</span>
						</div>
					</div>
					<span class="text-muted fs-sm">{{ $t("writeCalendarDesc") }}</span>
					<div class="fs-sm mt-4">
						<div v-if="!writeCalendar.length" class="d-inline-block py-2 px-3 rounded-pill" style="background-color: #ff001a1a">
							<div class="d-flex align-items-center">
								<BIcon icon="exclamation-circle" class="mr-2" variant="danger" scale="1.2" />
								<span class="text-muted">{{ $t("noWriteCalendar") }}</span>
							</div>
						</div>
						<div v-for="(provider, i) in writeCalendar" :key="i">
							<div class="d-flex align-items-center mb-2">
								<b-img :src="require(`../../assets/media/${provider.image}`)" width="16px"></b-img>
								<span class="text-muted ml-2">{{ provider.email }}</span>
							</div>
							<div v-for="(calendar, i) in provider.calendars" :key="i">
								<div class="d-flex align-items-center">
									<span class="mr-2 ml-3 rounded-circle" :style="{ backgroundColor: calendar.color, width: '8px', height: '8px' }"></span>
									<span>{{ calendar.name }}</span>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</section>
		<section class="mt-5">
			<div class="field_title pt-1 d-flex align-items-center">
				<BIcon icon="code" class="mr-2" scale="1.3" />
				<div class="title font-weight-500">{{ $t("connections") }}</div>
			</div>
			<div class="mt-3">
				<div class="pb-3">
					<div v-for="(calendar, i) of connections" :key="i" class="mb-4">
						<div class="d-flex align-items-center">
							<b-img :src="require(`../../assets/media/${calendar.image}`)" width="50px"></b-img>
							<div class="ml-3">
								<div class="mb-1 text-muted">{{ calendar.name }}</div>
								<div>{{ calendar.email }}</div>
							</div>
							<div class="ml-auto" v-if="calendar.hasDisconnect">
								<span class="btn_icon" v-b-tooltip.hover :title="$t('disconnect')">
									<b-iconstack font-scale="0.8">
										<b-icon stacked icon="triangle" shift-v="5"></b-icon>
										<b-icon stacked icon="dash-lg" shift-v="-6"></b-icon>
									</b-iconstack>
								</span>
							</div>
						</div>
					</div>
				</div>
				<button class="btn_outline d-flex align-items-center fs-sm" v-b-modal.CalendarConnectionModal>
					<BIcon icon="plus-lg" class="mr-2" />
					<span>{{ $t("addCalendarConnection") }}</span>
				</button>
			</div>
		</section>
		<CalendarConnectionModal />
		<ReadCalendarsModal />
		<WriteCalendarModal />
	</account>
</template>

<script>
	import Account from "@/components/Account";

	import CalendarConnectionModal from "@/components/modals/CalendarConnectionModal";
	import ReadCalendarsModal from "@/components/modals/ReadCalendarsModal";
	import WriteCalendarModal from "@/components/modals/WriteCalendarModal";

	export default {
		components: { Account, CalendarConnectionModal, ReadCalendarsModal, WriteCalendarModal },

		data() {
			return {
				calendarImages: {
					"microsoft.com": "microsoft.png",
					"google.com": "google_calendar.png",
				},
				calendarNames: {
					"microsoft.com": this.$t("outlookCalendar"),
					"google.com": this.$t("googleCalendar"),
				},
			};
		},

		mounted() {
			this.calendarsLoading = true;

			this.$store.dispatch("getCalendars").finally(() => {
				this.calendarsLoading = false;
			});
		},

		computed: {
			calendars() {
				return this.$store.state.account.calendars;
			},

			readCalendars() {
				let calendars = [];

				for (let integration of this.$me.integrations) {
					if (!integration.calendars || !integration.calendars.length || !integration.scopes.includes("calendar")) continue;

					let integrationCalendars = integration.calendars.filter((calendar) => calendar.shouldRead);

					if (!integrationCalendars.length) continue;

					let _integration = { email: integration.userEmail, image: this.calendarImages[integration.providerName], calendars: [] };

					calendarLoop: for (let integrationCalendar of integrationCalendars) {
						let calendar = this.calendars.find((cal) => cal.calendarId == integrationCalendar.calendarId);

						if (!calendar) continue calendarLoop;

						_integration.calendars.push({ calendarId: calendar.calendarId, name: calendar.calendarName, color: calendar.color || "#000000de" });
					}

					if (!_integration.calendars.length) continue;

					calendars.push(_integration);
				}

				return calendars;
			},

			writeCalendar() {
				let calendars = [];

				for (let integration of this.$me.integrations) {
					if (!integration.calendars || !integration.calendars.length || !integration.scopes.includes("calendar")) continue;

					let integrationCalendars = integration.calendars.filter((calendar) => calendar.shouldWrite);

					if (!integrationCalendars.length) continue;

					let _integration = { email: integration.userEmail, image: this.calendarImages[integration.providerName], calendars: [] };

					calendarLoop: for (let integrationCalendar of integrationCalendars) {
						let calendar = this.calendars.find((cal) => cal.calendarId == integrationCalendar.calendarId && cal.userId == integration.userId);

						if (!calendar) continue calendarLoop;

						_integration.calendars.push({ calendarId: calendar.calendarId, name: calendar.calendarName, color: calendar.color });
					}

					if (!_integration.calendars.length) continue;

					calendars.push(_integration);
				}

				return calendars;
			},

			connections() {
				let calendars = [];

				for (let calendar of this.calendars) {
					if (!calendar.isDefaultCalendar) continue;

					calendars.push({
						name: this.calendarNames[calendar.providerName],
						image: this.calendarImages[calendar.providerName],
						email: calendar.userEmail,
						hasDisconnect: !this.isAuthProviderCalendar(calendar.userId),
					});
				}

				return calendars;
			},
		},

		methods: {
			isAuthProviderCalendar(userId) {
				return !!this.$me.authProviders.find((provider) => provider.userId == userId);
			},
		},
	};
</script>

<style lang="scss"></style>
