<template>
	<b-modal id="ReadCalendarsModal" dialog-class="full_modal no_static_height" hide-footer hide-header centered size="lg" @hidden="reset" @shown="shown">
		<template #default>
			<div class="header d-flex justify-content-between align-items-center">
				<span class="fs-lg">{{ $t("readCalendars") }}</span>
				<BIcon icon="x" @click="reset" scale="0.8" class="btn_icon btn_icon_md text-muted" />
			</div>
			<div class="p-2 pb-5">
				<p class="fs-sm text-muted my-4 pb-1 pt-2">{{ $t("readCalendarsDesc") }}</p>
				<div class="mt-4">
					<div v-for="(provider, i) in providers" :key="i" class="mb-4">
						<div class="d-flex align-items-center mb-2">
							<b-img :src="require(`../../assets/media/${provider.image}`)" width="22px"></b-img>
							<span class="text-muted ml-2">{{ provider.email }}</span>
						</div>
						<div v-for="(calendar, i) in provider.calendars" :key="i" class="py-2">
							<div class="d-flex align-items-center ml-3">
								<b-form-checkbox v-model="calendar.shouldRead"> {{ calendar.name }} </b-form-checkbox>
							</div>
						</div>
					</div>
				</div>
			</div>
			<div class="footer d-flex align-items-center justify-content-end">
				<b-btn class="filter_btn active mx-3" size="sm" @click="reset">{{ $t("cancel") }}</b-btn>
				<b-btn type="submit" variant="primary" @click="save">{{ $t("save") }}</b-btn>
			</div>
		</template>
	</b-modal>
</template>

<script>
	export default {
		data() {
			return {
				isLoading: false,
				providers: [],
				calendarImages: {
					"microsoft.com": "microsoft.png",
					"google.com": "google_calendar.png",
				},
			};
		},

		computed: {
			calendars() {
				return this.$store.state.account.calendars;
			},
		},

		methods: {
			save() {
				this.$bvModal.hide("ReadCalendarsModal");
			},

			shown() {
				let providers = [];

				for (let integration of this.$me.integrations) {
					if (!integration.scopes.includes("calendar")) continue;

					let provider = { email: integration.userEmail, userId: integration.userId, image: this.calendarImages[integration.providerName], calendars: [] };

					calendarLoop: for (let calendar of this.calendars) {
						if (calendar.userId != integration.userId) continue calendarLoop;

						let integrationCalendar = integration.calendars.find((c) => c.calendarId == calendar.calendarId);

						let shouldRead = integrationCalendar ? integrationCalendar.shouldRead : false;

						provider.calendars.push({ calendarId: calendar.calendarId, shouldRead: shouldRead, name: calendar.calendarName });
					}

					providers.push(provider);
				}

				this.providers = providers;
			},

			reset() {
				this.$bvModal.hide("ReadCalendarsModal");
			},
		},
	};
</script>

